@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap');

.inicio-card-container {
    /* background-color: #CFE3E8; */
    display: flex;
    flex-direction: row;
    /* Dirección predeterminada */
    padding: 1rem;
    align-items: stretch;
    /* Asegura que los elementos llenen el contenedor en altura */
}

.inicio-texto {
    width: 50%;
    padding: 1rem;
    /* Añade padding para mejor separación del texto */
}

.inicio-imagen {
    background-image: url('../../imagenes/inicio.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 50%;
    /* Asegúrate de que esto está descomentado */
    height: 100vh;
    padding: 1rem;
    /* Consistencia en el padding */
}

/* Media Query para dispositivos de menos de 768px */
@media (max-width: 768px) {
    .inicio-card-container {
        flex-direction: column;
        /* Elementos en columna para pantallas pequeñas */
    }

    .inicio-texto,
    .inicio-imagen {
        width: 100%;
        /* Ocupan todo el ancho disponible */
        margin: 0;
        /* Elimina los márgenes adicionales */
        padding: 0.5rem;
        /* Ajusta el padding para no consumir demasiado espacio */
    }

    .inicio-imagen {
        height: 50vh;
        /* Reduce la altura en dispositivos móviles */
    }
}

.custom-font-title {
    font-family: 'Anton', sans-serif;
    /* Asegúrate de que el nombre de la fuente coincida con lo que importaste */
    font-size: 24px;
    /* Cambia esto según el tamaño deseado */
    color: aliceblue;
}

.custom-font-sub-title{
    font-family: 'Oxygen', sans-serif;
    /* Asegúrate de que el nombre de la fuente coincida con lo que importaste */
    font-size: 24px;
    /* Cambia esto según el tamaño deseado */
    color: aliceblue;
}