.beneficiosDestacados-card-container{
    background-color: #CFE3E8;
    display: flex;
    flex-direction: row;
}

.beneficiosDestacados-sub-container{
    width: 50%;
    margin: 1rem;
    display: flex;
    gap: 1rem;
    flex-direction: column;
}

.beneficiosDestacados-imagen{
    background-image: url('../../imagenes/beneficiosDestacados.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 50%;
    height: 100vh;
    margin: 1rem;
}




