.App-header {
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url('../../../imagenes/beneficiosDestacados.jpg');
  /* Añade una superposición oscura */
  background-repeat: repeat;
  /* La imagen se repite */
  background-position: center;
  /* Centra la imagen en el contenedor */
  background-attachment: fixed;
  /* La imagen de fondo no se mueve al desplazarse */
  min-height: 100vh;
}

.main-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}